@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css?family=Gloria+Hallelujah);
@import url(https://fonts.googleapis.com/css?family=Gochi+Hand);
@import url(https://fonts.googleapis.com/css?family=Indie+Flower);
@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Rubik);
@import url(https://fonts.googleapis.com/css?family=Satisfy);
.carousel-inner > .item {
  position: relative;
  display: none;
  transition: 0.1s ease-in-out left;
}
.icon {
  font-size: 100px;
  color: #333 !important;
}

.icon-main-posts {
  font-size: 100px;
  color: #333 !important;
}

.icon-top-posts {
  font-size: 40px;
  color: #333 !important;
}

.icon-text-light {
  font-size: 40px;
  color: #E9E9E9;
}

.fa-secondary{
  opacity:.3
}

/* IMAGE */
.image-company {
  max-height: 200px;
  max-width: 100%;
}

.image-company-type {
  height: 50px;
  width: 50px;
}

.image-education {
  height: 200px;
  width: 200px;
}

.image-education-landscape {
  height: 30px;
}

.image-feature {
  height: 130px;
}

.image-gray {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  transition: .3s ease-in-out;
}
.image-gray:hover {
  -webkit-filter: grayscale(0);
  filter: grayscale(0);
}

.image-main-icon {
  height: 30px;
}

.image-main-menu-logo:hover {
  opacity: 0.5;
}

.image-personal-me {
  height: 300px;
  width: 300px;
}

.image-zoon-icon:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition: .3s ease-in-out;
}
/* BACKGROUND */
.background-dark-main-header {
    background-color: #111;
}

.background-prefooter-dark-light {
    background-color: #F3F3F3;
}

.background-dark {
    background-color: #111;
}

/* BADGE */
.bg-dark {
    background-color: #111 !important;
}

/* BROWSER BARS */
::-webkit-scrollbar {
    width: 10px;
}
  
::-webkit-scrollbar-track {
    background: #A9A9A9; 
}
   
::-webkit-scrollbar-thumb {
    background: #111; 
}
  
::-webkit-scrollbar-thumb:hover {
    background: #777; 
}

/* CARD */
.card-job {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 15px 0 rgba(0, 0, 0, 0.12);
    transition: .25s box-shadow;
}
.card-job:hover {
    box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 30px 0 rgba(0, 0, 0, 0.15);
}

.card-link:hover {
    background-color: #FCFCFC !important;
    border-style: none;
    color: #111;
    
    transition: .5s box-shadow;

    box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 30px 0 rgba(0, 0, 0, 0.15);
}

.card-link-leak:hover {
    color: #111;
    
    transition: .5s box-shadow;

    box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.18), 0 4px 30px 0 rgba(0, 0, 0, 0.15);
}

/* FACEBOOK */
.fb_iframe_widget_fluid_desktop, .fb_iframe_widget_fluid_desktop span, .fb_iframe_widget_fluid_desktop iframe {
    max-width: 100% !important;
    width: 100% !important;
}

/* FOOTER */
.footer-big-page {
    background-color: #111;
    box-shadow: 0px 10px 10px 30px #111; 
    
    bottom: 0;
    left: 0;
    right: 0;
    
    position: relative;

    margin-top: 200px;
}

.footer-small-page {
    background-color: #111;
    
    bottom: 0;
    left: 0;
    right: 0;
    
    position: absolute;

    margin-top: 200px;
}

.footer {
    background-color: #111;
    clear:both;
    position: relative;
}

/* LINK */
a {
    color: #111;
    text-decoration: none;
} 

a:hover {
    color: #111;
    text-decoration: none;
}  

/* LOADER */
.overlay {
    height: 100%;
    width: 100%;
    position: fixed;
    z-index: 9999;
    left: 0;
    top: 0;
    background-color:#333;
    background-color: rgba(0,0,0, 0.9);
    overflow-x: hidden; /* Disable horizontal scroll */
    transition: 0.5s; /* 0.5 second transition effect to slide in or slide down the overlay (height or width, depending on reveal) */
}

/* SESSION */
.content-20 {
    height: 20%;
}

.content-30 {
    height: 30%;
}

.content-70 {
    height: 70%;
}

.content-80 {
    height: 80%;
}

.full-dark-h {
    background-color: #111;
    
    margin-left: 0rem !important;
    margin-right: 0rem !important;

    /* box-shadow: 0px 10px 5px 15px #111; */
}

.full-gray-h {
    background-color: #E9E9E9;
    
    margin-left: 0rem !important;
    margin-right: 0rem !important;

    box-shadow: 0px 20px 20px 15px #E9E9E9;
}

/* TRACE */
.underline {
    content: '';
    display: block;
    
    border-style: solid;
    border-top: 1px;

    width: 30px;
}
.underline-light.underline {
    border-color: #F9F9F9;
}
.underline-dark.underline {
    border-color: #333;
}

.underline-separator {
    content: '';
    display: block;
    
    border-style: solid;
    border-top: 1px;

    width: 99%;
}
.underline-separator-light.underline-separator {
    border-color: #F9F9F9;
}
.underline-separator-dark.underline-separator {
    border-color: #333;
}

/* UTILS */
body {
    background-color: #F9F9F9;    
    
    min-height: 100%;    
    
    position: relative;
}

html {
    scroll-behavior: smooth;
}
.nav-link {    
    color: #555;

    border: 0px;
    border-style: none;

    border-radius: 0%;
}

.nav-link.active {
    position: relative;

    border-style: none;

    background-color: #111 !important;
    color: #F9F9F9 !important;
    
    font-weight: bold;

    border-radius: 0%;
}

.nav-link:hover {    
    border-style: none;
    background-color: #111 !important;
    color: #F9F9F9 !important;

    border-radius: 0%;
}

.table-dark {
    background-color: #111 !important;
}

.table-subject {
    color: #999;
    background-color: #F9F9F9 !important;
}

.table-subject:hover{
    text-shadow: 0 0 1px rgba(0,0,0,0.5);
}

thead {
    background-color: #111 !important; 
}

.table-active {
    color: #F9F9F9;
    text-shadow: 0 0 2px rgba(249,249,249,1);
}

/* COLUMNS */
.col-finish {
    text-align: center;
    width: 10%;
}

.col-hours {
    text-align: center;
    width: 10%;
}

.col-level {
    text-align: center;
    width: 20%;
}

.col-skill {
    width: 80%;
}

.col-subject {
    width: 90%;
}
