    font-family: 'Open Sans', sans-serif;
    letter-spacing: 3px;
}

/* FONT - TITLE */
.font-text-title {
    font-family: 'Open Sans', sans-serif;
}

/* TEXT - ARROW */
.arrow-dark { color: #111 !important; }
.arrow-light { color: #F9F9F9 !important; }

/* TEXT - COLOR */
