.text-gray { color: #111 !important; } 
.text-gray:hover { color: #666 !important; }
.text-green { color: #28A745 !important; }
.text-orange { color: #d3850f !important; }
.text-pink { color: #e05eb9 !important; }
.text-purple { color: #7b508b !important; }
.text-red { color: #DC3545 !important; }
.text-yellow { color: #FFC107 !important; }

/* TEXT - FOOTER */
.text-footer-dark { color: #777 !important; } 
.text-footer-dark:hover { color: #999 !important; }
.text-footer-dark-title { color: #999 !important; }
.text-footer-dark-title:hover { color: #BBB !important; }

.text-describe-dark {
    color: #444 !important;
    letter-spacing: 1px;
}

.text-describe-light {
    color: #E9E9E9 !important;
    letter-spacing: 1px;
}

/* TEXT - MARK */
.mark-indicator-dark { color: #111 !important; }
.mark-indicator-light { color: #F9F9F9 !important; }

/* TEXT - TITLE */
.text-title-dark {
    color: #333 !important;
    letter-spacing: 2px;
}
.text-title-dark:hover { color: #111 !important; }

.text-title-light {
    color: #F9F9F9 !important;
    letter-spacing: 5px;
}
.text-title-light:hover { color: #D9D9D9 !important; }

.text-subtitle-dark {
    color: #333 !important;
    letter-spacing: 2px;
}
.text-subtitle-dark:hover { color: #111 !important; }

.text-subtitle-light {
    color: #D9D9D9 !important;
    letter-spacing: 2px;
}
.text-subtitle-light:hover { color: #B9B9B9 !important; }

/* TEXT */
.text-hover:hover { font-weight: bold; }

.text-light { color: #F9F9F9 !important; }
.text-light:hover { color: #D9D9D9 !important; }


